import { Statistic, Row, Col } from 'antd';

export default function ResultsPage(props) {
  return (
    <div style={{width: '100%', maxWidth: '70%', margin: 'auto', fontSize: '30px', marginBottom: '50px'}}>
      <h2>
        Your measurements
      </h2>

      <Row align="middle">
        <Col span={12}>
          <Statistic title="Temple Width" value={props.templeWidth} />
        </Col>

        <Col span={12}>
          <Statistic title="Nose Bridge Width" value={props.noseBridgeWidth} />
        </Col>
      </Row>
    </div>
  );
}
